@keyframes grow {
  0% {
    height: 0px;
    opacity: 0;
  }

  25% {
    opacity: 1;
  }

  100% {
    height: 400px;
  }
}

@keyframes mini-grow {
  0% {
    height: 0px;
    opacity: 0;
  }

  25% {
    opacity: 1;
  }

  100% {
    height: 200px;
  }
}

@keyframes fade {
  0% {
    opacity: 0;
  }

  70% {
    opacity: 0%;
  }

  100% {
    opacity: 100%;
  }
}

body {
  margin: 0;
  font-family: 'Roboto', 'Montserrat', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(25, 25, 25);
  color: whitesmoke;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1 {
  text-align: center;
}

.Form {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

form {
  display: flex;
  flex-direction: column;
  width: 40%;
  min-width: 185px;
  max-width: 300px;
  margin: 0 10px 50px 0;
}

label {
  margin: 10px 0;
  width: 100%
}

textarea {
  resize: none;
  box-sizing: border-box;
  width: 100%;
  height: 90px;
  border: none;
  border-bottom: 1px solid white;
  background-color: rgb(25, 25, 25);
  color: whitesmoke;
  margin: 5px 0 0 0;
  padding: 10px;
}

input {
  width: 100%;
  box-sizing: border-box;
  margin: 5px 0 0 0;
  border: none;
  border-bottom: 1px solid white;
  background-color: rgb(25, 25, 25);
  color: whitesmoke;
  padding: 10px;
}

#trunfo {
  width: 13px;
  margin: 0 10px;
}

select {
  padding: 2px 10px 2px 1px;
  margin-left: 5px;
  background-color: rgb(25, 25, 25);
  color: whitesmoke;
}

.cardDiv {
  display: flex;
  height: 400px;
  margin: 0 10px;
}

.card {
  background-color: rgb(255, 255, 255);
  box-shadow: 0 0 4px 2px rgb(0, 0, 0);
  border-radius: 9px;
  height: 400px;
  width: 300px;
  min-width: 250px;
  padding: 10px;
  user-select: none;
  animation: grow 1s;
}

.insideBorder {
  display: flex;
  background-color: #27476e;
  border-radius: 9px;
  height: 400px;
  justify-content: center;
  align-items: center;
  animation: grow 1s;
}

.insideCard {
  background-color: #27476e;
  border-radius: 8px;
  height: 380px;
  width: 280px;
  animation: fade 1s;
}

.title {
  background-color: #001d4a;
  display: flex;
  color: #eca400;
  text-align: end;
  text-transform: uppercase;
  box-sizing: border-box;
  padding: 0 10px;
  font-weight: 600;
  width: 100%;
  height: 35px;
  border-radius: 8px 8px 0 0;
  font-size: 20px;
}

.cardImage {
  width: 280px;
  position: absolute;
}

.cardInfo {
  background-color: #27476e;
  box-sizing: border-box;
  border-top: 6px solid #001d4a;
  padding-top: 3px;
  color: white;
  font-weight: 300;
  font-size: 11px;
  position: relative;
  width: 100%;
  height: 195px;
  top: 150px;
  border-radius: 0 0 9px 9px;
}

.cardAtt {
  position: absolute;
  top: 60px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  background-color: #eca400;
  box-sizing: border-box;
  padding: 13px;
  color:#001d4a;
  width: 100%;
  border-radius: 0 0 9px 9px;
  height: 129px;
  font-weight: 500;
  font-size: 20px;
  font-style: italic;
}

.cardAtt span {
  text-decoration: underline;
  margin: 5px 0;
}

.trunfoLogo {
  position: absolute;
  width: 100px;
  top: 14px;
  right: 20px;
}

.cardRare {
  color: white;
  font-weight: 300;
  font-size: 12px;
  align-self: flex-start;
  padding-top: 20px;
}

.title i {
  align-self: flex-end;
  margin-left: auto;
}

.trunfoLogo-test {
  display: none;
}

.btn41-43 {
  padding: 10px 25px;
  font-weight: 500;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  background: transparent;
  outline: none !important;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  display: inline-block;
}

.btn-41 {
  border: 2px solid rgb(255, 255, 255);
  z-index: 1;
  color: white;
}

.btn-41:after {
  position: absolute;
  content: "";
  width: 0;
  height: 100%;
  top: 0;
  left: 0;
  direction: rtl;
  z-index: -1;
  background: rgb(255, 255, 255);
  transition: all 0.3s ease;
}

.btn-41:hover {
  color: rgb(0, 0, 0);
}

.btn-41:hover:after {
  left: auto;
  right: 0;
  width: 100%;
}

.btn-41:active {
  top: 2px;
}

button {
  width: 100%;
}

.btn-41:disabled:after {
  background-color: rgb(83, 83, 83);
}

.btn-41:disabled:hover {
  color: white;
}

.btn-41:disabled {
  background-color: rgb(83, 83, 83);
  cursor: not-allowed;
}

.maxPoints {
  font-weight: 300;
  font-size: 12px;
  margin-top: 5px;
}

.savedCards {
  display: flex;
  box-sizing: border-box;
  padding: 10px;
  flex-wrap: wrap;
}

.savedCards h1 {
 width: 100%;
}

.savedCards .card {
  margin: 7px;
}

.haveTrunfo {
  font-size: 14px;
  margin: 10px 0;
}
  
 .button-45 {
   justify-content: center;
   background-color: rgb(214, 0, 0);
   border: 1px solid rgb(196, 0, 0);
   border-radius: 11px;
   box-sizing: border-box;
   color: white;
   cursor: pointer;
   display: flex;
   font-weight: 700;
   transition: border .2s ease-in-out;
   user-select: none;
 }
 
 .button-45:active, .button-45:active:hover {
   background-color: #001027;
   box-shadow: rgba(0, 0, 0, 0.329) 0 1px 3px 0 inset;
   color: #eca400;
   border-color: #001d4a;
 }
 
 .button-45:hover {
   background-color: #001d4a;
   color: #eca400;
   border-color: #001d4a;
 }
 
.filters {
  box-sizing: border-box;
  padding: 10px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin-bottom: 50px;
}

.filters label input:disabled, .filters label select:disabled {
  cursor: not-allowed;
}

.filters label {
  margin: 10px;
  width: 30%;
  min-width: 150px;
}

.checkFilter {
  width: 15px;
  margin-left: 5px;
}

.filters h2, .filters h1, .filters h3 {
  width: 100%;
  text-align: center;
}

.gamebtn {
  width: 50%;
  min-width: 100px;
  align-self: center;
  margin: auto;
  padding-bottom: 50px;
}

.handBox {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 30px 0;
}

.cardMini {
  background-color: rgb(255, 255, 255);
  box-shadow: 0 0 3px 1px rgb(0, 0, 0);
  border-radius: 4px;
  height: 200px;
  width: 150px;
  padding: 4px;
  user-select: none;
  margin: 7px;
  animation: mini-grow 0.7s;
}

.insideBorderMini {
  display: flex;
  background-color: #27476e;
  border-radius: 4px;
  height: 200px;
  justify-content: center;
  align-items: center;
  animation: mini-grow 0.7s;
}

.insideCardMini {
  background-color: #27476e;
  border-radius: 4px;
  height: 190px;
  width: 140px;
  animation: fade 0.7s;
}

.titleMini {
  background-color: #001d4a;
  display: flex;
  color: #eca400;
  text-align: end;
  text-transform: uppercase;
  box-sizing: border-box;
  padding: 0 2px;
  font-weight: 600;
  width: 100%;
  height: 18px;
  border-radius: 2px 2px 0 0;
  font-size: 14px;
}

.titleMini i {
  align-self: flex-end;
  margin-left: auto;
}

.cardImageMini {
  width: 140px;
  position: absolute;
}

.cardInfoMini {
  background-color: #27476e;
  box-sizing: border-box;
  border-top: 1px solid #001d4a;
  padding-top: 1px;
  color: white;
  font-weight: 300;
  font-size: 4px;
  position: relative;
  width: 100%;
  height: 100px;
  top: 75px;
  border-radius: 0 0 2px 2px;
}

.cardAttMini {
  position: absolute;
  display: flex;
  justify-content: center;
  flex-direction: column;
  background-color: #eca400;
  box-sizing: border-box;
  padding: 4px;
  color:#001d4a;
  width: 100%;
  border-radius: 0 0 2px 2px;
  height: 95px;
  font-weight: 500;
  font-size: 14px;
  font-style: italic;
}

.cardAttMini span {
  text-decoration: underline;
  margin: 5px 0;
}

.trunfoLogoMini {
  position: absolute;
  width: 45px;
  top: 25px;
  right: 10px;
}

.hoverMiniCard:hover {
  cursor: pointer;
  outline: 1px solid #eca400;
  outline-offset: 1px;
  box-shadow: 0 0 6px 0 #eca400;
}

.handInput {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  margin-bottom: 15px;
}

.handInput select {
  height: 25px;
  margin: 0 5px;
}

.handInput h1 {
  width: 100%;
  margin: 10px 0;
}

.handInput h2 {
  text-align: center;
  margin: 10px 20px;
}

.game-stats {
  margin-left: 50px;
  margin-top: 50px;
  font-weight: 500;
  font-style: italic;
}

.game-stats p {
  margin: 5px 0;
}

.cpuAtt {
  text-transform: uppercase ;
  font-weight: 400;
  font-style: italic;
  color: #eca400;
}

.myTurnTitle {
  margin: 10px 5px !important;
}

.cardsBattle {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.battleSec {
  display: flex;
  width: 100vw;
  flex-wrap: wrap;
  margin-bottom: 30px;
}

.winTitle, .loseTitle, .drawTitle {
  width: 100%;
  text-align: center;
  margin: 20px 10px;
  flex-direction: row;
}

.winTitle {
  color: rgb(66, 219, 66);
}

.loseTitle {
  color:rgb(236, 50, 50);
}

.drawTitle {
  color: #8A8A8A;
}

.battle-buttons {
  width: 100%;
  display: flex;
  justify-content: center;
  margin: 30px 0;
}

.battle-buttons button {
  width: 40%;
  min-width: 150px;
  max-width: 200px;
}

.resultSec {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.resultSec h2 {
  width: fit-content;
}

.Form form {
  margin: 5px;
}

.Form .card {
  margin: 5px;
}

.resultImg {
  max-width: 90%;
  margin-top: 20px;
}

@media screen and (max-width: 375px) {
  .cardsBattle {
    flex-direction: column;
  }

  .cardsBattle h2 {
    margin: 5px;
  }

  .card {
    display: flex;
    background-color: rgb(255, 255, 255);
    box-shadow: 0 0 3px 1px rgb(0, 0, 0);
    border-radius: 4px;
    height: 200px;
    width: 150px !important;
    max-width: fit-content;
    padding: 4px;
    user-select: none;
    margin: 7px;
    animation: mini-grow 0.7s;
    min-width: 150px;
  }
  
  .insideBorder {
    display: flex;
    background-color: #27476e;
    border-radius: 4px;
    height: 200px;
    width: 150px;
    justify-content: center;
    align-items: center;
    animation: mini-grow 0.7s;
  }
  
  .insideCard {
    background-color: #27476e;
    border-radius: 4px;
    height: 190px;
    width: 140px;
    animation: fade 0.7s;
  }
  
  .title {
    background-color: #001d4a;
    display: flex;
    color: #eca400;
    text-align: end;
    text-transform: uppercase;
    box-sizing: border-box;
    padding: 0 2px;
    font-weight: 600;
    width: 100%;
    height: 18px;
    border-radius: 2px 2px 0 0;
    font-size: 14px;
  }
  
  .cardImage {
    width: 140px;
    position: absolute;
  }
  
  .cardInfo {
    background-color: #27476e;
    box-sizing: border-box;
    border-top: 1px solid #001d4a;
    padding-top: 1px;
    color: white;
    font-weight: 300;
    font-size: 4px;
    position: relative;
    width: 100%;
    height: 100px;
    top: 75px;
    border-radius: 0 0 2px 2px;
  }
  
  .cardAtt {
    position: absolute;
    top: 0px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    background-color: #eca400;
    box-sizing: border-box;
    padding: 4px;
    color:#001d4a;
    width: 100%;
    border-radius: 0 0 2px 2px;
    height: 95px;
    font-weight: 500;
    font-size: 14px;
    font-style: italic;
  }
  
  .cardAtt span {
    text-decoration: underline;
    margin: 5px 0;
  }
  
  .trunfoLogo {
    position: absolute;
    width: 45px;
    top: 25px;
    right: 10px;
  }
  
  .cardRare {
    display: none;
  }
  
  .title i {
    align-self: flex-end;
    margin-left: auto;
  }
}

@media screen and (max-width: 700px) {
  .savedCards {
    justify-content: center;
  }
}
